<template>
  <div>
    <Index
      v-if="currentPage == 0"
      ref="page1"
      id="index"
      class="page"
      :userInfo="userInfo"
    />

    <Login v-if="currentPage == 1" ref="login2" id="login" class="page" />
  </div>
</template>

<script>
import Index from "./components/page/index.vue";
import Login from "./components/page/Login/Login.vue";
import bus from "./js/EventBus.js";

export default {
  components: {
    Index,
    Login,
  },
  data() {
    return {
      currentPage: 0,
      userInfo: null,
    };
  },
  created() {
    bus.$on("changePage", (page) => {
      this.currentPage = page;
    });
    bus.$on("token", (userInfo) => {
      this.userInfo = userInfo;
    });
  },
};
</script>
